.searchbox{
  min-width: 600px;
  -webkit-box-shadow: 0 0 3px rgba(0, 0, 0, 0.25);
          box-shadow: 0 0 3px rgba(0, 0, 0, 0.25);
  padding:2rem 5rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  background: #ffffff;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, 100%);
      -ms-transform: translate(-50%, 100%);
          transform: translate(-50%, 100%);
}
.searchbox__title{
  margin: 0.5rem;
  font-weight: 400;
  color: #252525;
  font-size: 2rem;
}
.searchbox label{
  color: #888;
  visibility: hidden;
}
#eventSearch{
  border-radius: 2px;
  border: 1px solid #ccc;
  width: 75%;
  height: 2.5rem;
  font-size: 1rem;
}

@media (max-width: 500px){
  .searchbox{
    min-width: 100%;
    padding: 1rem;
  }
}


